@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
.about-container {
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),
		0 -6px 16px -6px rgba(0, 0, 0, 0.025);
	margin: 0 auto;
	margin-top: 100px;
	padding: 20px;
	max-width: 100%;
	background-color: white;
	border-radius: 5px;
	height: 50px;
	align-items: center;
}

.about-container span {
	display: block;
	font-size: 15px;
}

.movies .movie {
	box-sizing: content-box;
	width: 45%;
	background-color: white;
	margin-bottom: 70px;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	padding: 20px;
	position: relative;
	color: #b2b3bc;
	border-radius: 5px;
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),
		0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies img {
	position: relative;
	top: -50px;
	max-width: 150px;
	width: 100%;
	margin-right: 30px;
	box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3),
		0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie-title,
.movie .movie-year {
	margin: 0;
	font-weight: 300;
}

.movie .movie-title {
	margin-bottom: 5px;
	font-size: 20px;
	color: #2c2c2c;
}

.movie .movie-genres {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
	margin: 5px 0px;
}

.movie-genres li,
.movie .movie-year {
	margin-right: 10px;
	font-size: 13px;
}

.movie .movie-rating {
	font-size: 12px;
	font-weight: 300;
}

.movie .expand-button {
	background: none !important;
	border: none;
	padding: 0 !important;
	color: #069;
	text-decoration: underline;
	cursor: pointer;
}

.movie .expand-button:focus {
	outline: none;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	margin: 0;
	padding: 0;
	font-size: 10px;
	font-family: 'Monserrat', sans-serif;
	background-color: #eff3f7;
	height: 100%;
}

body,
#root,
.container {
	height: 100%;
	display: flex;
	justify-content: center;
}

/* Loading */
.loader {
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.loader-text {
	font-size: 20px;
}

.spinner {
	color: black;
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
	margin-top: 20px;
}

.spinner div {
	transform-origin: 40px 40px;
	animation: spin 1.2s linear infinite;
}

.spinner div::after {
	content: '';
	display: block;
	position: absolute;
	top: 3px;
	left: 37px;
	width: 5px;
	height: 18px;
	background: gray;
	border-radius: 20%;
}

.spinner div:nth-child(1) {
	transform: rotate(0deg);
	animation-delay: -1.1s;
}

.spinner div:nth-child(2) {
	transform: rotate(30deg);
	animation-delay: -1s;
}

.spinner div:nth-child(3) {
	transform: rotate(60deg);
	animation-delay: -0.9s;
}

.spinner div:nth-child(4) {
	transform: rotate(90deg);
	animation-delay: -0.8s;
}

.spinner div:nth-child(5) {
	transform: rotate(120deg);
	animation-delay: -0.7s;
}

.spinner div:nth-child(6) {
	transform: rotate(150deg);
	animation-delay: -0.6s;
}

.spinner div:nth-child(7) {
	transform: rotate(180deg);
	animation-delay: -0.5s;
}

.spinner div:nth-child(8) {
	transform: rotate(210deg);
	animation-delay: -0.4s;
}

.spinner div:nth-child(9) {
	transform: rotate(240deg);
	animation-delay: -0.3s;
}

.spinner div:nth-child(10) {
	transform: rotate(270deg);
	animation-delay: -0.2s;
}

.spinner div:nth-child(11) {
	transform: rotate(300deg);
	animation-delay: -0.1s;
}

.spinner div:nth-child(12) {
	transform: rotate(330deg);
	animation-delay: 0s;
}

.movies {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	margin-top: 6rem;
	padding: 50px;
	padding-top: 70px;
	width: 80%;
}

/* Keyframes */
@keyframes spin {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

.nav-container {
	font-size: 16px;
	font-family: 'Montserrat', sans-serif;
	width: 100vw;
	height: 6rem;
	position: fixed;
	background-color: darkgray;
	z-index: 1;
}

.navbar {
	display: flex;
	height: 100%;
	justify-content: space-between;
	align-items: center;
	background-color: #333333;
}

#navbar-logo {
	margin-left: 2rem;
	color: #45a148;
}

.nav-menu {
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
	align-items: center;
	height: 5rem;
}

.link {
	color: white;
	font-size: 20px;
	text-decoration: none;
	margin-right: 3rem;
	text-transform: uppercase;
}

